@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,900;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-wrapper {
  font-family: 'Rubik';
  display: grid;
  grid-template-areas:
  'menu header header header'
  'menu main main main';
  grid-template-columns: 1fr 5fr;
  grid-template-rows: 55px 10fr;
  height: 100vh;
}
.header {
    grid-area: header;
    background-color: #50CAFF;
    height: 55px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 40px;
    padding-right: 40px;
}

.header_1 {
    font-weight: 400;
    font-size: 28px;
    line-height: 33px;
    color: #FFFFFF;
}

.header_2 {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.avatar {
    border-radius: 50%;
    border: 1px solid #FFFFFF;
    margin-right: 20px;
}

.header_3 {
    width: 18px;
    height: 18px;
    margin-right: 20px;
}

.profile {
    grid-area: main;
    background: #FCFCFC;
    padding-top: 15px;
    padding-left: 20px;
    position: relative;
}

.profile_1 {
    margin-left: 20px;
    font-size: 16px;
    line-height: 19px;
    font-weight: 400;
    margin-bottom: 15px;
}

.profile_1.two {
    margin-top: 35px;
}

.items {
    position: relative;
    width: 455px;
    height: 174px;
    padding-left: 20px;
    box-shadow: 0px 0px 5px #8f7fdf;
    border-radius: 5px;
    margin-right: 20px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    margin-bottom: 15px;
}

.items_1 p {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    display: inline-block;
    margin-right: 6px;
    margin-top: 20px;
    margin-bottom: 5px;
}

.items_2 {
    max-width: 280px;
    font-weight: 300;
    font-size: 14px;
    line-height: 20px;
}

.items_3 {
    display: -webkit-flex;
    display: flex;
    margin-top: 10px;
}

.items_3 img {
    border-radius: 50%;
    margin-right: 10px;
}

.items_3 div span {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
}

.items_3 div {
    margin-top: 12px;
    font-size: 14px;
    line-height: 17px;
    color: #9488db;
}


.itemss {
    display: -webkit-flex;
    display: flex;
    overflow: hidden;
    width: 980px;
}


.cards {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.card {
    width: 500px;
    display: -webkit-flex;
    display: flex;
    border: 2px solid #50CAFF;
    box-shadow: 0px 0px 5px #EBE7FF;
    border-radius: 5px;
    -webkit-align-items: center;
            align-items: center;
    margin-right: 16px;
    margin-bottom: 15px;
}

.card1 {
    width: 133px;
    border-radius: 5px;
}

.card1 svg {
    margin-top: 77px;
    margin-left: 42px;
}

.card img {
    margin-top: 77px;
    width: 40px;
    height: 40px;
    margin-left: 42px;
    margin-right: 42px;
}

.card2 {
    font-size: 28px;
    line-height: 33px;
    margin-left: 36px;
    margin-right: 36px;
}

.card3 {
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
    margin-bottom: 20px;
    -webkit-padding-start: 14px;
            padding-inline-start: 14px;
    font-size: 16px;
    line-height: 24px;
    list-style: none;
}

.card3 li::before {
    display: inline-block;
    width: 1em;
    margin-left: -1em;
    content: "\2022";
    color:#50CAFF;
}

.card1 {
    height: 100%;
    background-color: #50CAFF;
}

.card2 hr {
    height: 0px;
    background-color: #50CAFF;
    border: 2px solid #50CAFF;
}

.hidden {
    display: none;
}


.open .itemss {
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow-y: scroll;
    height: 646px;
    width: 505px;
    padding-left: 10px;
    padding-top: 10px;
    margin-top: 47px;
}

.open .itemss::-webkit-scrollbar {
    width: 8px;
}

.open .itemss::-webkit-scrollbar-track {
    border-radius: 5px;
    background-color: #EBE7FF;
}

.open .itemss::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #003B72;
}

.up {
    position: absolute;
    top: 30px !important;
    left: 40px;
}


.calendar {
    width: 573px;
    height: 481px;
    background: #EBE7FF;
    border-radius: 5px;
}

.DayPicker-wrapper , .DayPicker-Months {
    height: 100%;
}

.DayPicker-Week {
    height: 63px;
}

.DayPicker-Day {
    width: 79px;
    font-size: 20px;
    line-height: 20px;
    font-weight: 400px;
    border-radius:0% !important;
    background-color: #fff;
}

.DayPicker-Day--outside {
    background: #EBE7FF;
}

.DayPicker-Month {
    margin: 0px !important;
}

.DayPicker-Caption {
    height: 57px;
    background: #7761FF;
}

.DayPicker-Caption div {
    text-align: center;
    margin-top: 20px;
    color: #FFFFFF;
}

.DayPicker-NavButton--prev {
    top: 18px !important;
    left: 198px !important;
    background-image: none !important;
}

.DayPicker-NavButton--prev:after {
    content: '\02C2';
    color: #fff;
  }

.DayPicker-NavButton--next:after {
    content: '\02C3';
    color: #fff;
  }

.DayPicker-NavButton--next {
    top: 18px !important;
    right: 198px !important;
    background-image: none !important;
}

.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
    background-color: #fff !important;
    color: black !important;
}

.small {
    position: absolute;
    bottom: 0px;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background: #50CAFF;
    font-size: 14px;
    line-height: 22px;
    color: #FFFFFF;
}

.cal {
    width: 573px;
    height: 481px;
    position: absolute;
    top: 48px;
    right: 40px;
}

.del {
    cursor: pointer;
    width: 98px;
    height: 40px;
    background: #7761FF;
    border-radius: 5px;
    background: #7761FF;
    border-radius: 5px;
    color: #FFFFFF;
    border: none;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    position: absolute;
    bottom: 20px;
    right: 20px;
}

.more {
    position: absolute;
    right: 75px;
    top: 130px;
}

.number {
    position: absolute;
    top: 90px;
    right: 70px;
}
.menu { 
    grid-area: menu;
    background-color: #003B72;
    padding-top: 10px;
}

.item {
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 14px;
    padding-bottom: 14px;
    padding-left: 20px;
    color: #FFFFFF;
    font-size: 14px;
    line-height: 17px;
    cursor: pointer;
}


.item svg {
    margin-right: 14px;
}

.active {
    background-color:#FFFFFF;
    color: #7761FF;
}

.active svg path {
    fill: #7761FF;
}



  
